* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


body{
  height: 100vh;
  width: 100vw;
  background-image: linear-gradient(150deg, #43cea2, #185a9d);
}

.taskFlex{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 2em;
}

.taskFlex h1, .name, .desc{
  margin-top: 1.5em;
}

.taskFlex button{
  padding: .3em 2.7em;
  margin-top: 1em;
  margin-bottom: 1.5em;
  font-size: 1em;
  box-shadow: 0.2em 0.2em 1em;
  border: none;
  border-radius: 4px;
  width: 100%;
  height: 6vh;
  cursor: pointer;
}

.taskFlex button:hover{
  box-shadow: 0.3em 0.3em 2em;
  font-size: 1.1em;
  cursor: pointer;
}

.taskFlex .name, .desc{
  font-size: 1.2em;
}

.name label {
  margin-right: 0.3em;
}

.name input{
  font-size: 1em;
  padding-left: 0.2em;
}

.desc textarea{
  margin-top: 1em;
  font-size: 1em;
  padding-left: 0.2em;
}

.taskRow{
  margin: 1.3em auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border: solid black 0.1px;
  border-radius: 5px;
  box-shadow: .1em .1em 2em;
  width: 80%;
  height: 8vh;
}

.taskRow:hover{
  box-shadow: .4em .4em 3em;
}


.taskRow button{
  margin-left: .5em;
  border: none;
  font-size: 0.8em;
  background: none;
}

.taskRow button:hover{
  font-size: .9em;
  cursor: pointer;
}

.colFlex{
  display: flex;
  flex-direction: column;
}

.colFlex .right-top{
  padding-top: 1.1em;
  margin-bottom: 0.5em;
  margin-right: 0.4em;
}

.colFlex .right-bot{
  font-size: 0.1em;
  opacity: 0.5;
}


.popup {
  position: absolute;
  left: 20%;
  top: 35%;
  height: 25vh;
  width: 60vw;
  background-color: white;
  border-radius: 5px;
  box-shadow: .8em 0.5em 5em;
  border: solid 0.2px black;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
}

.popup p {
  border: solid black 0.1px;
  height: 10vh;
  width: 50vw;
  padding-left: 0.3em;
}

.popup button{
  width: 10vw;
  border-radius: 3px;
  border: solid black 0.1px;
  cursor: pointer;
}

.popup button:hover{
  box-shadow: 0.2em 0.3em 1em;
}



@media only screen and (min-width: 600px) {

  .taskFlex h1 {
    font-size: 2.5em;
  }

  .taskFlex .name, .desc{
    font-size: 1.6em;
  }


}